html, body, #root {
  height: 100%;
}

.App {
  text-align: center;
  height: 100%;
  display: grid;
  grid-template-areas: 
  'Title Title'
  'MainInfo MainInfo'
  'MainInfo MainInfo';
  grid-template-columns: 75% 25%;
  grid-template-rows: 10% 15% auto;
}

/*Title*/
.Title {
  grid-area: Title;
  background-color: #3C438A;
  color: #FFF;
  display: grid;
  grid-template-areas: 
  'Name Menu'
  'Motto Menu';
  grid-template-columns: 40% 60%;
  grid-template-rows: 75% 25%;
  position: fixed;
  width: 100%;
  height: 10%;
  font-size: 1.5em;
}

.Title div {
  font-size: 1rem;
}

.Title span {
  grid-area: Motto;
  height: 100%;
  font-size: 0.6rem;
}

nav {
  z-index: 2;
  grid-area: Menu;
  background-color: #3C438A;
  color: #FFF;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  align-content: space-around;
}

nav button {
  box-sizing: border-box;
  max-height: 75%;
  min-height: 50%;
  width: 10%;
  border: none;
  margin-left: 2.5%;
  margin-right: 2.5%;

  border-radius: 10%;

  grid-area: Button;

  background-color: #4b53a8;
  color: #FFF;
  font-size: 1.25rem
}

nav button:hover {
  color: #3b4398 !important;
  background-color: #EEE !important;
}

.Active {
  color: #5b63b8 !important;
  background-color: white !important;
}

.Menu {
  display: none;
}

/*Mobile*/ 

@media only screen and (orientation: portrait) {
  .Menu {
    display: block;
  }

	nav.Closed .Menu {
    width: 75%
  }

  nav.Open {
    position: fixed;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    align-content: space-around;
  }

  nav.Open button {
    box-sizing: border-box;
    max-height: 30%;
    min-height: 10%;
    width: 75%;
    border: none;
    margin-left: 2.5%;
    margin-right: 2.5%;
  
    border-radius: 10%;
  
    grid-area: Button;
  
    background-color: #4b53a8;
    color: #FFF;
    font-size: 1.25rem
  }

  button.Closed {
    display: none;
  }

  .ImageGrid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: 200px;
  
    justify-content: space-evenly;
  
    background-color: #282c34;
  
    width: 67%;
    min-height: 40%;
    max-height: fit-content;
  }
  
  .ImageGrid img {
    width: 90%;
    height: 90%;
    margin-left: auto;
    margin-right: auto;
  }
}

/*Main*/

.Main {
  grid-area: MainInfo;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100%;
  height: fit-content;
  max-height: fit-content;
}

main {
  height: 100%;
  max-width: 90%;
  background-color: #00000060;
  color: white;

  display: flex;
  flex-direction: column;
  align-items: center;
}

section {
  height: fit-content;
}

section .text {
  font-size: 200%;
  height: fit-content;
}

section .img {
  max-width: 40%;
  top: 0;
}

.LeftText .img{
  float: right;
  height: 85%;
}

.RightText .img{
  float: left;
  height: 85%;
}

.RightText {
  width: 95%

}

.WhiteGround {
  background-color: white;
  color: black;
}

.ImageGrid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: 200px;

  justify-content: space-evenly;

  background-color: #282c34;

  width: 67%;
  min-height: 40%;
  max-height: fit-content;
}

.ImageGrid img {
  width: 90%;
  height: 90%;
  margin-left: auto;
  margin-right: auto;
}

/*Old*/

.ImgInfoBox {
  background-color: #DDD;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.SubBox {
  background-color: #7c85da;
  min-height: 10%;
  max-height: fit-content;
  width: 90%;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.SubBox * {
  width: 95%
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}